.BuildControls {
    display: flex;
    width: 100%;
    background-color: #CF8F2E;
    flex-flow: column;
    align-items: center;
    margin: auto 0 0;
    padding: 10px 0;
    box-shadow: 0 2px 1px #ccc;
}

.OrderButton {
    background-color: #DAD735;
    outline: none;
    cursor: pointer;
    border: 1px solid #966909;
    color: #966909;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    box-shadow: 2px 2px 2px #966909;
    margin-top: 10px;
}

.OrderButton:hover, .OrderButton:active {
    background-color: #A0DB41;
    border: 1px solid #966909;
    color: #966909;
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.OrderButton:not(:disabled) {
    animation: enable 0.3s linear;
}

@keyframes enable {
    0% {
        transform: scale(1);
    }
    60% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}
@media (min-width: 992px) {
    .BuildControls {
        width: 350px;
        justify-content: center;
        margin: 0;
    }
    .OrderButton {
        margin-top: 30px;
    }
}