.BreadBottom {
    height: 30px;
    width: 80%;
    background: linear-gradient(#F08E4A, #e27b36);
    border-radius: 0 0 30px 30px;
    box-shadow: inset -15px 0 #c15711;
    margin: 2px auto;
}

.BreadTop {
    height: 40px;
    width: 80%;
    background: linear-gradient(#bc581e, #e27b36);
    border-radius: 50% 50% 0 0;
    box-shadow: inset -15px 0 #c15711;
    margin: 2px auto;
    position: relative;
}

.Seeds1 {
    width: 10%;
    height: 15%;
    position: absolute;
    background-color: white;
    left: 30%;
    top: 50%;
    border-radius: 40%;
    transform: rotate(-20deg);
    box-shadow: inset -2px -3px #c9c9c9;
}

.Seeds1:after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: white;
    left: -170%;
    top: -260%;
    border-radius: 40%;
    transform: rotate(60deg);
    box-shadow: inset -1px 2px #c9c9c9;
}

.Seeds1:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: white;
    left: 180%;
    top: -50%;
    border-radius: 40%;
    transform: rotate(60deg);
    box-shadow: inset -1px -3px #c9c9c9;
}

.Seeds2 {
    width: 10%;
    height: 15%;
    position: absolute;
    background-color: white;
    left: 64%;
    top: 50%;
    border-radius: 40%;
    transform: rotate(10deg);
    box-shadow: inset -3px 0 #c9c9c9;
}

.Seeds2:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: white;
    left: 150%;
    top: -130%;
    border-radius: 40%;
    transform: rotate(90deg);
    box-shadow: inset 1px 3px #c9c9c9;
}


.Meat {
    width: 80%;
    height: calc(7px + 1.5vw);
    background: linear-gradient(#7f3608, #702e05);
    margin: 2px auto;
    border-radius: 15px;
}

.Cheese {
    width: 90%;
    height: calc(3px + 1vw);
    margin: 2px auto;
    background: linear-gradient(#f4d004, #d6bb22);
    border-radius: 20px;
}

.Salad {
    width: 85%;
    height: calc(4px + 1.3vw);
    margin: 2px auto;
    background: linear-gradient(#228c1d, #91ce50);
    border-radius: 20px;
}

.Bacon {
    width: 80%;
    height: calc(2px + 0.75vw);
    background: linear-gradient(#bf3813, #c45e38);
    margin: 2px auto;
}
@media (min-width: 450px) {
    .BreadTop {
        height: 55px;
    }
}
@media (min-width: 769px) {
    .BreadTop {
        height: 75px;
    }
    .BreadBottom {
        height: 45px;
    }
    .Meat, .Bacon, .Cheese, .Salad{
        margin: 4px auto;
    }
}
@media (min-width: 992px) {
    .BreadTop {
        height: 85px;
    }
    .BreadBottom {
        height: 55px;
    }
}